import {useState, useContext} from "react";
import ThemeSettingContext from "../contexts/ThemeSettingContext";
import FontSizeSettingContext from "../contexts/FontSizeSettingContext";
import BibleContext from "../contexts/BibleContext";
import { CloseIcon } from "./AppIcons";
import { collapseMenu } from "./OverlayMenu";

function Settings() {
    const { handleTheme, theme } = useContext(ThemeSettingContext);
    const { handleFontSize, theFontSize } = useContext(FontSizeSettingContext);
    const { handleTestament, testament } = useContext(BibleContext);

    const [currentTheme, setCurrentTheme] = useState(theme);
    const [currentFontSize, setCurrentFontSize] = useState(theFontSize);
    const [currentTestament, setCurrentTestament] = useState(testament);

    const handleChange = (event) => {
        const {name, value} = event.target;
        if (name === "themes") {
            setCurrentTheme(value);
            handleTheme(value);
        } else if(name === "font-zoom") {
            setCurrentFontSize(value);
            handleFontSize(value);
        } else if ( name === "randomVerse") {
            setCurrentTestament(value);
            handleTestament(value);
        }
    }

    const handleSubmit = (event) => {
        event.preventDefault();
    }

    return (
        <div onClick={collapseMenu}>
            <div className="close-page"><CloseIcon /></div>
            <div className="settings" onSubmit={handleSubmit}>
              <div className="settings-item">
                <label htmlFor="themes">Tema</label>
                <select name="themes" value={ currentTheme } onChange={handleChange}>
                  <option value="nightTheme">Noćna</option>
                  <option value="dayTheme">Dnevna</option>
                  <option value="sepiaTheme">Sepia</option>
                </select>
              </div>
            <div className="settings-item">
                <label htmlFor="font-zoom">Veličina slova</label>
                <select name="font-zoom" value={currentFontSize} onChange={handleChange}>
                    <option value="fontZoomM">M</option>
                    <option value="fontZoomL">L</option>
                    <option value="fontZoomXL">XL</option>
                    <option value="fontZoomXXL">2XL</option>
                    <option value="fontZoomXXXL">3XL</option>
                </select>
            </div>
              <div className="settings-item">
                <label htmlFor="randomVerse">Slučajan redak naslovnice iz </label>
                <select name="randomVerse" value={ currentTestament } onChange={handleChange}>
                  <option value="new">Novog Zavjeta</option>
                  <option value="old">Starog Zavjeta</option>
                  <option value="both">Cijele Biblije</option>
                </select>
              </div>
            </div>
        </div>
    )
}

export default Settings;